<template>
  <div v-if="currentBook" class="book-details py-3">
    <v-container class="details-container">
      <v-row>
        <v-col cols="12">
          <!-- Displays the book cover image -->
          <v-img :src="currentBook.img" alt="Book Cover" contain></v-img>
        </v-col>
      </v-row>

      <!-- Book details rows -->
      <v-row>
        <v-col cols="12">
          <h2>{{ currentBook.title }}</h2>
          <p><strong>Author:</strong> {{ currentBook.author }}</p>
          <p><strong>Format:</strong> {{ currentBook.bookformat }}</p>
          <p><strong>ISBN:</strong> {{ currentBook.isbn }}</p>
          <p>
            <strong>Genre:</strong>
            <v-chip v-for="(genre, index) in currentBook.genre.split(',')" :key="index" class="mr-2" outlined>
              {{ genre.trim() }}
            </v-chip>
          </p>
          <p><strong>Pages:</strong> {{ currentBook.pages }}</p>
          <p><strong>Rating:</strong> {{ currentBook.rating }}</p>
          <p><strong>Reviews:</strong> {{ currentBook.reviews }}</p>
          <p><strong>Price:</strong> {{ currentBook.price }}</p>
          <p v-if="showAdminBoard"><strong>Status:</strong> {{ currentBook.published ? "Published" : "Pending" }}</p>
          <p><strong>Description:</strong> {{ currentBook.description }}</p>
        </v-col>
      </v-row>

      <!-- Divider for styling -->
      <v-divider class="my-5"></v-divider>

      <!-- Row for admin buttons -->
      <v-row>
        <v-col cols="12">
           <!-- Conditionally shows buttons if the user is an admin -->
          <div v-if="showAdminBoard">
            <!-- Button to toggle published status -->
            <v-btn v-if="currentBook.published" @click="updatePublished(false)" color="primary" small class="mr-2">
              UnPublish
            </v-btn>

            <v-btn v-else @click="updatePublished(true)" color="danger" small class="mr-2">
              Publish
            </v-btn>

             <!-- Button to delete the book -->
            <v-btn color="danger" small class="mr-2" @click="deleteBook">
              Delete
            </v-btn>
             <!-- Button to update the book details -->
            <v-btn color="danger" small @click="updateBook">
              Update
              </v-btn>
          
          </div>
          <!-- Buttons for all users -->
          <div v-if="currentUser">
            <!-- Button to initiate purchase flow -->
            <v-btn color="danger" small @click="checkower">
              Buy
            </v-btn>
            <!-- Button to add the book to cart -->
            <v-btn color="danger" small @click="addToCart">
              Add to Cart
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>

  <!-- Displayed if no book is selected -->
  <div v-else>
    <p>Please click on a Book...</p>
  </div>
</template>

<script>
import BookDataService from "../services/BookDataService";

export default {
  name: "book",
  data() {
    return {
      // The current book object
      currentBook: null,
    };
  },
  methods: {
     // Fetches book data from the server
    getBook(id) {
      BookDataService.get(id)
          .then((response) => {
            this.currentBook = response.data;
            console.log(response.data);
          })
          .catch((e) => {
            console.log(e);
          });
    },

    // Updates the published status of the book
    updatePublished(status) {
      var data = {
        id: this.currentBook.id,
        title: this.currentBook.title,
        description: this.currentBook.description,
        published: status,
      };

      // Calls the API to update the book
      BookDataService.update(this.currentBook.id, data)
          .then((response) => {
            this.currentBook.published = status;
            console.log(response.data);
          })
          .catch((e) => {
            console.log(e);
          });
    },

     // Updates the current book's details
    updateBook() {
      BookDataService.update(this.currentBook.id, this.currentBook)
          .then((response) => {
            console.log(response.data);
            this.$router.push({name: "books"});
          })
          .catch((e) => {
            console.log(e);
          });
    },
    // Checks if the current user is the owner of the book
    checkower() {
      var data = {
        userid: this.currentUser.id,
        bookid: this.currentBook.id,
      };
      BookDataService.checkowner(data)
          .then((response) => {
            console.log(response.data);
            if (response.data === 0) {
              this.$router.push({name: "buybookpage", params: {id: this.currentBook.id}});
            } else {
              alert("you already owned this book");
            }
          })
          .catch((e) => {
            console.log(e);
          });
    },
    // Deletes the current book
    deleteBook() {
      BookDataService.delete(this.currentBook.id)
          .then((response) => {
            console.log(response.data);
            this.$router.push({name: "books"});
          })
          .catch((e) => {
            console.log(e);
          });
    },
    // Adds the current book to the shopping cart
     addToCart() {
      this.$store.commit("addToCart", this.currentBook);
      alert("Book added to cart!");
    },
  },
  // Fetches book details when the component is mounted
  mounted() {
    this.getBook(this.$route.params.id);
  },
  // Computed properties for reactive data
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    // Determines if the current user is an admin
    showAdminBoard() {
      if (this.currentUser && this.currentUser.roles) {
        return this.currentUser.roles.includes('ROLE_ADMIN');
      }
      return false;
    }
  },
};
</script>

<style scoped>
.book-details {
  /* Styling for the book details section */
  padding: 20px;
  max-width: 600px;
  margin: auto;
  //background-color: rgba(0, 0, 0, 0.8); /* 设置半透明背景 */
}

.book-details v-img {
  /* Ensures the image takes the full width */
  max-width: 100%;
}
</style>
